.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;

  @media (min-width: @desktop-width) {
    padding: 0;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  max-width: 400px;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  background-color: @base;
  color: @main;
  border: 2px solid @main;
  border-radius: 10px;
  padding: 10px;
  margin: 0 auto;

  &:hover {
    color: @base;
    background-color: @main;
    border-color: @base;
  }

  @media (min-width: @desktop-width) {
    font-size: 18px;
    padding: 10px;
  }
}
